import { getMvSublist } from '@/request/api/collect';
export default {
  data() {
    return {
      // 用户收藏的视频/MV
      mvs: []
    };
  },

  async mounted() {
    if (sessionStorage.getItem("cookie")) {
      const res = await getMvSublist(sessionStorage.getItem("cookie")); // console.log('获取收藏的视频列表', res);

      this.mvs = res.data.data; // console.log('收藏的视频/MV列表', this.mvs);
    }
  },

  methods: {
    // 对播放量的处理
    formatCount(num) {
      return this.$formatCount(num);
    },

    // 格式化播放时长
    formatDuration(time) {
      return this.$formatDuration(time);
    },

    // 去往 mv播放页/视频播放页
    goVideoPlayer(type, vid) {
      // type：1是 Mv，2是视频
      // 这里 item.type == 0 为 MV，不等于0则为视频
      if (type == 0) {
        this.$router.push({
          path: '/VideoPlayer',
          query: {
            mvid: vid,
            type: 1
          }
        });
      } else {
        this.$router.push({
          path: '/VideoPlayer',
          query: {
            videoid: vid,
            type: 2
          }
        });
      }
    },

    // 去往个人主页/歌手页
    goSingerPage(id) {
      // console.log(id.toString().length > 8); //true、false
      // id 位数大于 8 的为普通用户，去往个人主页，否则为歌手，去往歌手页
      if (id.toString().length > 8) {
        this.$router.push({
          path: '/PersonalHome',
          query: {
            userId: id
          }
        });
      } else {
        this.$router.push({
          path: '/SingerPage',
          query: {
            id: id
          }
        });
      }
    }

  }
};